import {
  SET_CURRENT_SONG,
  TOGGLE_RANDOM,
  TOGGLE_REPEAT,
  TOGGLE_PLAYING,
} from "./player-types";

const initialState = {}

export default (state = initialState, action) => {
  switch (action.type) {
    case SET_CURRENT_SONG:
      return {
        ...state,
        currentSong: action.data,
        playing: true,
      };
    case TOGGLE_RANDOM:
      return {
        ...state,
        random: action.data,
      };
    case TOGGLE_REPEAT:
      return {
        ...state,
        repeat: action.data,
      };
    case TOGGLE_PLAYING:
      return {
        ...state,
        playing: action.data,
      };

    default:
      return state;
  }
};
